<template>
  <div id="foot">
    <pre style="color: white; text-align: center; font-family: Arial; display: flex;">
      <b style="font-size: 14px; margin: auto; font-family: Arial;">
        Xiangyu Li Lab at Beijing Jiaotong University, Beijing 100044, China
      </b>
    </pre>
  </div>
</template>

<script>
export default {
  name: 'Contact'
}
</script>

<style scoped>
#foot {
  background: linear-gradient(to bottom, #005BAC, #020b80);
  height: auto; /*  */
  width: 100%;
  padding: 10px 0; /*  */
}

pre {
  margin: 0;
}

b {
  display: block;
  font-size: 14px;
  font-family: Arial;
  margin: auto;
  padding: 0;
  text-align: center;
  word-wrap: break-word; /*  */
  word-break: break-word; /*  */
  max-width: 100%; /*  */
  white-space: normal; /*  */
}

/*  */
@media (max-width: 1024px) {
  b {
    font-size: 16px; /*  */
  }
}

@media (max-width: 768px) {
  b {
    font-size: 14px; /*  */
  }
}

@media (max-width: 390px) {
  #foot {
    padding: 15px 0; /*  */
  }

  b {
    font-size: 12px; /*  */
  }
}
</style>



