<template>
  <div class="photo-wall">
    <!--  -->
    <div class="photo-row">
      <div class="photo-item" v-for="(image, index) in images" :key="index">
        <img :src="image" alt="Photo" class="photo" @click="showImage(image)" />
      </div>
    </div>

    <!--  -->
    <div v-if="showModal" class="modal" @click="closeModal">
      <img :src="currentImage" alt="Enlarged Photo" class="enlarged-photo" />
    </div>
  </div>
</template>

<script>
export default {
  name: "Activities",
  data() {
    return {
      images: [
        require("../assets/memory6.jpg"),
        require("../assets/memory7.jpg"),
        require("../assets/memory5.jpg"),
        require("../assets/memory4.jpg"),
        require("../assets/memory2.jpg"),
        require("../assets/memory1.jpg"),
        require("../assets/all_people.jpg"),
        require("../assets/all_people1.jpg"),
      ],
      showModal: false, // 
      currentImage: "", // 
    };
  },
  methods: {
    showImage(image) {
      this.currentImage = image; // 
      this.showModal = true; // 
    },
    closeModal() {
      this.showModal = false; // 
      this.currentImage = ""; // 
    },
  },
};
</script>

<style scoped>
/*  */
.photo-wall {
  width: 1000px;
  margin: auto;
  display: flex;
  justify-content: center;
  padding: 20px;
}

/*  */
.photo-row {
  display: flex;
  flex-wrap: wrap; /*  */
  justify-content: center; /*  */
  gap: 20px; /*  */
  max-width: 1200px; /*  */
}

/*  */
.photo-item {
  width: 300px; /*  */
  height: 250px; /*  */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden; /*  */
  border: 2px solid #ddd; /*  */
  border-radius: 8px; /*  */
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1); /*  */
}

/*  */
.photo {
  width: 100%;
  height: 100%;
  object-fit: cover; /*  */
  cursor: pointer;
  transition: transform 0.3s ease;
}

.photo:hover {
  transform: scale(1.05);
}

/*  */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  cursor: pointer;
}

.enlarged-photo {
  max-width: 90%;
  max-height: 90%;
  border-radius: 10px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.3);
}

/*  */
@media screen and (max-width: 768px) {
  .photo-wall {
    width: 80%;
    margin: auto;
    display: flex;
    justify-content: center;
    padding: 20px;
  }

  .photo-item {
    width: 300px; /*  */
    height: 150px;
  }
}
</style>



